:root {
    --dark: #202020;
    --light: #ffffff;
    --thumb: #202020;
    --track: #ebebeb;
    --blue: #3b82f6;
    --sky: #60a5fa;
    --gray: #f0f0f0;
    --tent: rgba(0, 0, 0, 0.8);
    --violet: #744498;
    --green: #a0d468;
    --red: #cc284d;
    --orange: #f7b844;
    --border: #c0c0c0;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none !important;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    font-family: "Poppins", sans-serif;
    letter-spacing: -0.5px;
    background: var(--light);
    color: var(--dark);
}

a,
a:link,
a:visited {
    text-decoration: none;
    cursor: pointer;
    color: var(--primary);
}

a:hover {
    color: var(--primary);
}

button {
    cursor: pointer;
    font: inherit;
    border: none;
    background: transparent;
}

input,
textarea {
    border: none;
    -webkit-appearance: none;
    border-radius: 0;
}

body::-webkit-scrollbar {
    width: 7px;
}

body::-webkit-scrollbar-track {
    background: var(--light);
}

body::-webkit-scrollbar-thumb {
    background: var(--thumb);
    border-radius: 50px;
    background-clip: padding-box;
    border: 2px solid rgba(0, 0, 0, 0);
}

body::-webkit-scrollbar-thumb:hover {
    background: var(--thumb);
}


/* contact links */

.link {
    transition: all 0.3s;
}

.link:hover {
    transform: scale(1.2);
    color: var(--blue);
}



