.header {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 50px;
    transform: translateY(-100px);
}

.theme {
    height: 25px;
    cursor: pointer;
}

.theme-icons {
    gap: 10px;
}

.logo {
    user-select: none;
}

.dot {
    display: inline-block;
    color: var(--blue);
}