h1 {
    font-weight: 700;
    font-size: 33px;
    letter-spacing: -1px;
    line-height: 45px;
}

h2 {
    font-weight: 700;
    font-size: 25px;
    letter-spacing: -1px;
    line-height: 37px;
}

@media only screen and (min-width: 600px) {
    h1 {
        font-size: 45px;
        letter-spacing: -3px;
        line-height: 57px;
    }
    h2 {
        font-size: 30px;
        letter-spacing: -1.5px;
        line-height: 42px;
    }
}
@media only screen and (min-width: 768px) {
    h1 {
        font-size: 55px;
        letter-spacing: -3.5px;
        line-height: 67px;
    }
    h2 {
        font-size: 30px;
        letter-spacing: -2px;
        line-height: 42px;
    }
}
