#contact-form {
    margin-top: 60px;
    max-width: 400px;
    width: 100%;
}

.btn {
    background: var(--blue);
    color: #FFFFFF;
    line-height: 14px;
    width: 100px;
    height: 40px;
    padding: 7px 20px;
    font-size: 15px;
    border-radius: 5px;
    transition: all .5s;
    position: relative;
    overflow: hidden;
    -webkit-transform: translateZ(0);
}

.btn:focus,
.btn:active {
    outline: none !important;
    box-shadow: none;
}

.btn:hover {
    background: var(--sky);
}

.btn > span {
    position: relative;
    z-index: 2;
}

.btn.active::before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    left: -100%;
    width: 300%;
    height: 100%;
    background: var(--blue)
      repeating-linear-gradient(
      60deg,
        transparent,
        transparent 10px,
        var(--sky) 10px,
        var(--sky) 20px
      );
    
    animation: loading 1s infinite linear;
  }

@keyframes loading {
    0%{
        transform: translateX(25px);
    }
    100%{
        transform: translateX(-20px);
    }
}

/* form group */

.form-group {
    position: relative;
    display: flex;
    height: 48px;
    width: 100%;
    margin-bottom: 30px;
}

.fg-label {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    transform-origin: 0% 0%;
    color: var(--dark);
    padding: 0.25rem 0;
    pointer-events: none;
    transition: all 0.35s;;
}

.fg-underline {
    content: "";
    display: block;
    position: absolute;
    z-index: 1;
    left:0;
    bottom:0;
    transform: scaleX(0);
    width: 100%;
    height: 1px;
    background: var(--dark);
    transition: all 0.35s;
}

.fg-input {
    font:inherit;
    width:100%;
    height: inherit;
    border:0;
    border-bottom: 1px solid var(--border);
    outline: none;
    background: transparent;
    font-size: 1rem;
    transition: all 0.35s;
    -webkit-transform: translateZ(0);
    color: var(--dark);
}

.fg-input:focus ~ .fg-label, .fg-input:valid ~ .fg-label {
    color: var(--blue);
    transform: translate(0,-2.875rem) scale(0.875);
}

.fg-input:focus ~ .fg-underline {
    background: var(--blue);
    transform: scaleX(1);
}

/* Social Links */

.social {
    margin-top:60px;
    gap: 20px;
}