section {
    height: 100vh;
}

.container {
    max-width: 1000px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

.flex {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

.spacer {
    flex-grow: 1;
}

.center {
    align-items: center;
    justify-content: center;
}

.h-center {
    justify-content: center;
}

.v-center {
    align-items: center;
}

.flex-col {
    flex-direction: column;
}

.w-100 {
    width: 100%;
}

.h-100 {
    height: 100%;
}

/* positioning and spacing */

.overflow-hidden {
    overflow: hidden;
}

.relative {
    position: relative;
}

.absolute {
    position: absolute;
}

.padding {
    padding: 20px;
}

