.projects {
	grid-gap: 20px;
	display: grid;
	gap: 20px;
	grid-template-columns: 1fr;
}

@media only screen and (min-width: 600px) {
	.projects {
		grid-template-columns: 1fr 1fr;
	}
}

.project {
	background: var(--tent);
	border-radius: 5px;
	display: flex;
	flex-direction: column;
}

.project-title {
	line-height: 25px;
}

.project-subtitle {
	color: var(--blue);
	font-weight: 500;
	font-size: 12px;
}

.project-body {
	margin: 20px 0;
	flex: 1;
}

.project-details {
	display: flex;
	flex-direction: column;
}

.project-links {
	gap: 20px;
}

/* Project Image */

.project-image {
	width: 100%;
	min-height: 300px;
	width: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
}
