/* intro */

.intro {
    text-align: center;
}

.intro h1,
.intro p {
    transform: translateY(60px);
}

.hightlight {
    position: relative;
    margin: 0 5px;
    display: inline-block;
}

.hightlight-text {
    display: block;
    position: relative;
    z-index: 2;
    padding-bottom: 3px;
}

.hightlight-curtain {
    display: inline-block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background: var(--blue);
    border-radius: 50px;
}
